.menu {
	padding: 15px 18px 15px 17px;
	&-list {
		display: flex;
		flex-direction: column; }
	&__link {
		line-height: 35px;
		color: #333;
		font-weight: 500;
		font-size: 16px;
		position: relative;
		&:hover {
			background-color: #eee;
			color: #333;
			text-decoration: none; }
		&_disabled {
			color: #c4c4c4;
			cursor: default;
			pointer-events: none; } }
	&__icon {
		width: 26px;
		display: inline-block;
		height: 22px;
		background-image: url(../img/menu-icons.png);
		background-position: -13px -4px;
		vertical-align: middle; }
	&__count {
		position: absolute;
		right: 0;
		top: 6px;
		font-size: 12px;
		color: #f3792e;
		background-color: #e3e3e3;
		line-height: 23px;
		min-width: 23px;
		text-align: center;
		padding-left: 3px;
		padding-right: 3px;
		border-radius: 12px; } }

.menu__link {
	&_interests {
		.menu__icon {
			background-position: -13px -39px; } }
	&_friends {
		.menu__icon {
			background-position: -13px -39px; } }
	&_groups {
		.menu__icon {
			background-position: -13px -108px; } }

	&_events {
		.menu__icon {
			background-position: -13px -108px; } }

	&_learn {
		.menu__icon {
			background-position: -13px -143px; } }

	&_photos {
		.menu__icon {
			background-position: -13px -247px; } }

	&_videos {
		.menu__icon {
			background-position: -13px -279px; } }

	&_music {
		.menu__icon {
			background-position: -13px -315px; } }

	&_games {
		.menu__icon {
			background-position: -13px -350px; } }

	&_board {
		.menu__icon {
			background-position: -13px -178px; } }

	&_messages {
		.menu__icon {
			background-position: -13px -74px; } } }

.menu-ads {
	margin-top: 15px;
	margin-bottom: 15px;
	display: block;
	width: 170px;
	height: 170px;
	background-position: center;
	-webkit-background-size: contain;
	background-size: contain; }

.menu-bottom {
	width: 170px;
	&__link {
		color: #888888;
		font-size: 11px;
		display: inline-block;
		margin-right: 6px;
		&:hover {
			color: #888; }
		&:last-child {
			margin-right: 0; } } }
